.select-container {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;

  padding: 45px 0;
}

.mentor-select {
  width: 50%;
}
.mentee-table {
  width: 100%;
}

.mentee-table th,
.mentee-table td {
  padding: 5px;
  border: 1px solid #000000;
}

.mentee-table td[data-status] {
  font-size: 14px;
  color: transparent;
}

.mentee-table td[data-status]:hover {
  color: #000000;
}
